<template>
  <div id="register-container">
    <v-container>
      <v-frorm>
      <div class="from-group">
        <InputLabel label="Email" />
        <v-text-field v-model="email" outlined id="email" name="email"/>
      </div>
      <div class="from-group">
        <InputLabel label="First Name" />
        <v-text-field v-model="text" outlined id="firstname" name="firstname"/>
      </div>
      <div class="from-group">
        <InputLabel label="Last Name" />
        <v-text-field v-model="text" outlined id="firstname" name="lastname"/>
      </div>
      <div class="form-group">
        <InputLabel label="Password" />
        <v-text-field
          outlined
          id="password"
          :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
          :type="show ? 'text' : 'password'"
          class="input-group--focused"
          @click:append="show = !show"
          name="password"
        />
      </div>
      <button @click="addUser()" class="btn btn-success">Submit</button>
    </v-frorm>
    </v-container>
  </div>
</template>

<script>
import UsersData from '@/services/usersdata';

// Component
import InputLabel from '../components/text/InputLabel.vue';

export default {
  name: 'Register',

  components: {
    InputLabel,
  },

  data() {
    return {
      user: {
        email: '',
        firstname: '',
        lastname: '',
        password: '',
      },
      submitted: false,
    };
  },

  methods: {
    addUser() {
      const data = {
        email: this.user.email,
        firstname: this.user.firstname,
        lastname: this.user.lastname,
        password: this.user.password,
      };
      UsersData.create(data)
        .then((response) => {
          console.log(response.data);
          this.$router.push({ name: 'User List' });
        })
        .catch((e) => {
          console.log(e);
        });
    },

    newUser() {
      this.submitted = false;
      this.user = {};
    },
  },
};
</script>

